@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

[v-cloak] {
  display: none;
}

html, body {
  height: 100%;
  background-color: #100D5E;
  color: white;
  font-family: 'Noto Sans TC', sans-serif;
  overflow-y: hidden;
}

#app {
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  overflow-x: hidden;
  overflow-y: scroll;
  padding: 2rem 0;
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
}

#app::after {
  content: '';
  position: absolute;
  background: url('kmt.svg');
  background-size: contain;
  height: 30rem;
  width: 30rem;
  opacity: .5;
  right: 3rem;
  top: 3rem;
  z-index: 0;
  animation:spin 7s linear infinite;
}

#app * {
  z-index: 1;
}

h1 {
  display: flex;
  flex-direction: column;
  font-size: 4rem;
  text-shadow: 5px 5px 0px #E72410;
  color: white;
  font-weight: 700;
}

h1 span:first-child {
  font-size: 5.5rem;
  font-weight: 900;
}

.criminal-img {
  display: inline-block;
  position: relative;
  position: fixed;
  top: 0;
  transform: rotateZ(180deg);
}

.criminal-img::after {
  content: '';
  position: absolute;
  background-color: rgba(0,0,0,.8);
  height: 10%;
  width: 70%;
  top: 29%;
  left: 20%;
  transform: rotateZ(10deg);
  backdrop-filter: blur(5px)
}

.criminal-img img {
  width: 300px;
  height: auto;
}

.time {
  margin-bottom: 1rem;
}

.time .till {
  margin-top: 3rem;
  font-size: 2rem;
  font-weight: 500;
  text-shadow: 3px 3px 0px blue;
}

.time .count {
  font-size: 3rem;
  margin-right: 1rem;
  text-shadow: 3px 3px 0px grey;
  font-family: 'Black Ops One', 'Noto Sans TC', cursive;
}

.time .unit {
  font-size: 70%;
  font-weight: 700;
  text-shadow: none;
}

.since a {
  color: white;
  opacity: .8;
  font-style: italic;
  font-size: 1.5rem;
}

footer {
  margin-top: 3rem;
  text-align: center;
  font-size: 1.5rem;
}

footer a {
  color: white;
  opacity: .8;
}

@media screen and (max-width: 768px) {
  html {
    font-size: 12px;
  }
  #app {
    display: block;
  }
  .criminal-img {
    position: relative;
    transform: translateY(-3rem) rotateZ(180deg);
  }
  .criminal-img img {
    width: 180px;
  }
}